import React from 'react';
//import logo from './logo.svg';
import './App.css';

import Navigation from './Navigation';

import './assets/css/gradients.css';

import Library from './Library';
import Settings from './Settings';
import SplashScreen from './SplashScreen';
import Auth from './Auth';
import Referral from './Referral';

import { withTranslation } from 'react-i18next';

class App extends React.Component {

  constructor(){
    super();

    this.state = {
      mainComponent: 'library',
      renderSplashscreen: true,
      renderAuth: true,
    }

    this.changeComponent = this.changeComponent.bind(this);
  }

  handleClick(){
    this.setState({
      LibraryOn: false,
      SettingsOn: true
    });
  }

  changeComponent(component){
    this.setState({
      mainComponent: component
    });
  }

  componentDidMount(){
    setTimeout(() => {this.load()},3500);
    
  }

  load = () => {
    this.setState({
      renderSplashscreen: false
    });
  }

  renderComponent(state){
    switch(state.mainComponent){
      case "library":
        return <Library getChildClick={this.changeComponent} />;
      case "settings":
        return <Settings getChildClick={this.changeComponent} />;
      case "referral":
        return <Referral getChildClick={this.changeComponent} />;
      default:
        return <Library getChildClick={this.changeComponent} />;
    }
  }

  renderAuth(value){

    setTimeout(() => {this.renderAuthState(value)},500);
  }

  renderAuthState = (value) => {
    this.setState({
      renderAuth: value
    });
  }

  render(){

    {
      if(this.state.renderAuth){
        return (
          this.state.renderSplashscreen ? <SplashScreen /> : <Auth getChildClick={this.renderAuth.bind(this)} />
        );
      }else{
        return (
          <div className="App rootStyle">
      
            <div className="d-flex" id="wrapper">
        
              <Navigation getChildClick={this.changeComponent} />
      
              <main className="w-100" role="main">

                {this.renderComponent(this.state)}

              </main>
              {/*<div>
                    <button onClick={() => i18n.changeLanguage('es')}>es</button>
                    <button onClick={() => i18n.changeLanguage('en')}>en</button>
              </div>*/}
            </div>
      
          </div>
      
        );
      }
    }

  }

}

export default withTranslation('translations')(App);
