import React from 'react';
import $ from 'jquery';
import { withTranslation, Trans } from 'react-i18next';
import Switch from '@material-ui/core/Switch';
import {Link} from 'react-router-dom';

import LanguageIcon from '@material-ui/icons/Language';
import PersonIcon from '@material-ui/icons/Person';
import RecordVoiceOverIcon from '@material-ui/icons/RecordVoiceOver';
import BusinessIcon from '@material-ui/icons/Business';
import LiveHelpIcon from '@material-ui/icons/LiveHelp';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import CallIcon from '@material-ui/icons/Call';
import EmailIcon from '@material-ui/icons/Email';
import NotificationsIcon from '@material-ui/icons/Notifications';

import SelectLanguage from './SelectLanguage';
import ContentTopNav from './ContentTopNav';

class Settings extends React.Component {

    constructor(){
        super();

        this.state = {
            pageTitle: <Trans i18nKey='settings.title'></Trans>,
            checkedA: false,
            checkedB: true,
            checkedC: false,
            render:''
        }
    }

    componentDidMount(){
        $("#wrapper").toggleClass("toggled");

        $("#menu-toggle").click(function (e) {
            e.preventDefault();
           $("#wrapper").toggleClass("toggled");
       });
    }

    log(){
        console.log("click");
    }

    handleChange = (event) => {
        this.setState({
            [event.target.name]: event.target.checked 
        });
    };

    handleClick(component, e){
        console.log(component);
        this.setState({render:component});        
    }

    handler = (value) => {
        this.props.getChildClick(value);
    }

    _renderSubComp(){
        switch(this.state.render){
            case 'language': return <SelectLanguage />
            /*case 'bracelets' : return <Bracelets/>
            case 'rings': return <FRings/>*/
        }
    }

    render() {
        
        return (
            <div id="pag e-content-wrapper">
                <ContentTopNav title={this.state.pageTitle} />

                <div className="container-fluid">
                    <a onClick={this.handleClick.bind(this, 'language')}>
                        <div className="row-12 my-3 ml-2">
                            <div className="d-flex justify-content-start">
                                <span className="menubtn mr-4" >
                                    <LanguageIcon />
                                </span>
                                <span className="text-left" style={{color:'white', fontWeight:'bold'}}><Trans i18nKey='settings.language'></Trans></span>
                            </div>
                            <div className="d-flex justify-content-end">
                                <span className="text-left text-muted" style={{color:'white', fontWeight:'bold', marginTop:'-22px',fontSize:'80%'}}><Trans i18nKey='settings.selectedLang'></Trans></span>
                                <span style={{marginTop:'-25px'}}><ArrowForwardIosIcon style={{fill: 'white'}} /></span>
                            </div>
                        </div>
                    </a>
                    {this._renderSubComp()}

                    <a onClick={this.log}>
                        <div className="row-12 my-3 ml-2">
                            <div className="d-flex justify-content-start">
                                <span className="menubtn mr-4">
                                    <PersonIcon />
                                </span> <span style={{color:'white', fontWeight:'bold'}}><Trans i18nKey='settings.profile'></Trans></span>
                            </div>
                            <div className="d-flex justify-content-end">
                                <span style={{marginTop:'-25px'}}><ArrowForwardIosIcon style={{fill: 'white'}} /></span>
                            </div>
                        </div>
                    </a>

                    <div className="my-5">
                        <a>
                            <div className="row-12 my-3 ml-2">
                                    <div className="d-flex justify-content-start">
                                        <span className="menubtn mr-4">
                                            <CallIcon />
                                        </span> <span style={{color:'white', fontWeight:'bold'}}><Trans i18nKey='settings.call'></Trans></span>
                                    </div>
                                    <div className="d-flex justify-content-end">
                                        <span style={{marginTop:'-35px'}}>
                                            <Switch
                                                checked={this.state.checkedA}
                                                onChange={this.handleChange}
                                                color="default"
                                                name="checkedA"
                                                inputProps={{ 'aria-label': 'primary checkbox' }}
                                            />    
                                        </span>
                                    </div>
                            </div>
                        </a>

                        <a>
                            <div className="row-12 my-3 ml-2">
                                    <div className="d-flex justify-content-start">
                                        <span className="menubtn mr-4">
                                            <EmailIcon />
                                        </span> <span style={{color:'white', fontWeight:'bold'}}><Trans i18nKey='settings.email'></Trans></span>
                                    </div>
                                    <div className="d-flex justify-content-end">
                                        <span style={{marginTop:'-35px'}}>
                                            <Switch
                                                checked={this.state.checkedB}
                                                onChange={this.handleChange}
                                                color="default"
                                                name="checkedB"
                                                inputProps={{ 'aria-label': 'primary checkbox' }}
                                            />    
                                        </span>
                                    </div>
                            </div>
                        </a>

                        <a>
                            <div className="row-12 my-3 ml-2">
                                    <div className="d-flex justify-content-start">
                                        <span className="menubtn mr-4">
                                            <NotificationsIcon />
                                        </span> <span style={{color:'white', fontWeight:'bold'}}><Trans i18nKey='settings.push'></Trans></span>
                                    </div>
                                    <div className="d-flex justify-content-end">
                                        <span style={{marginTop:'-35px'}}>
                                            <Switch
                                                checked={this.state.checkedC}
                                                onChange={this.handleChange}
                                                color="default"
                                                name="checkedC"
                                                inputProps={{ 'aria-label': 'primary checkbox' }}
                                            />    
                                        </span>
                                    </div>
                            </div>
                        </a>

                    </div>

                    <a style={{cursor:'pointer'}} onClick={() => this.handler('referral')}>
                        <div className="row-12 my-3 ml-2">
                            <div className="d-flex justify-content-start">
                                <span className="menubtn mr-4">
                                    <RecordVoiceOverIcon />
                                </span> <span style={{color:'white', fontWeight:'bold'}}><Trans i18nKey='settings.referral'></Trans></span>
                            </div>
                            <div className="d-flex justify-content-end">
                                <span style={{marginTop:'-25px'}}><ArrowForwardIosIcon style={{fill: 'white'}} /></span>
                            </div>
                        </div>
                    </a>

                    <a onClick={this.log}>
                        <div className="row-12 my-3 ml-2">
                            <div className="d-flex justify-content-start">
                                <span className="menubtn mr-4">
                                    <BusinessIcon />
                                </span> <span style={{color:'white', fontWeight:'bold'}}><Trans i18nKey='settings.corporate'></Trans></span>
                            </div>
                            <div className="d-flex justify-content-end">
                                <span style={{marginTop:'-25px'}}><ArrowForwardIosIcon style={{fill: 'white'}} /></span>
                            </div>
                        </div>
                    </a>
                    
                    <a onClick={this.log}>
                        <div className="row-12 my-3 ml-2">
                            <div className="d-flex justify-content-start">
                                <span className="menubtn mr-4">
                                    <LiveHelpIcon />
                                </span> <span style={{color:'white', fontWeight:'bold'}}><Trans i18nKey='settings.help'></Trans></span>
                            </div>
                            <div className="d-flex justify-content-end">
                                <span style={{marginTop:'-25px'}}><ArrowForwardIosIcon style={{fill: 'white'}} /></span>
                            </div>
                        </div>
                    </a>

                </div>
            </div>
        );
    }
}

export default withTranslation('translations')(Settings);